.SectionTitle{
  max-width: 180px;
  /* background-color: #f9f9f9; */
  border-bottom: solid 3px #aaa;
  position: relative;
  margin: 0 auto 30px;
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  padding: 12px;
}

.SectionTitle:not(:first-child){
  margin: 80px auto 30px;
}

.SectionTitle:after {
  position: absolute;
  content: " ";
  display: block;
  border-bottom: solid 3px grey;
  bottom: -3px;
  left: 0;
  width: 20%;
}

.Forward{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 50px;
}

.Note{
  max-width: 250px;
  margin: 0 auto;
  text-align: center;
}


.Result{
  margin-top: 10px;
  height: 500px;
  background-color: #000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.Result h2{
  font-size: 40px;
  text-align: center;
  margin-bottom: 5px;
}

