.Logo{
    background-color: white;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-box-shadow: 0 0 20px #888;
            box-shadow: 0 0 20px #888;
    border-radius: 50%;
    -ms-flex: none;
        flex: none;
}


.Logo.Small{
  height: 100px;
  width: 100px;
}
.Logo.ExtraSmall{
  height: 30px;
  width: 30px;
  margin-right: 10px
}

.Logo.Medium{
  height: 120px;
  width: 120px;
}
.Logo.Large{
  height: 300px;
  width: 300px;
}

.Logo img {
    height: 100%;
}

@media(min-width: 768px){
  .Logo.Medium{
    height: 150px;
    width: 150px;
  }
}



