.Icon{
  width: 30px;
  height: 30px;
}

.MenuIcon{
  width: 30px;
  height: 30px;
}

.CloudIcon{
  width: 40px;
  height: 40px;
  fill: #696969;
  cursor: pointer;
}


@media(min-width: 768px){
  .MenuIcon{
    width: 50px;
    height: 50px;
  }
  .CloudIcon:hover{
    fill: #000;
  }
}



